.wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
    min-height: 30px;
    line-height: 30px;
    color: #757575;
}

.number {
    -ms-flex: 0 0 30px;
        flex: 0 0 30px;
    text-align: right;
}

.title {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 40%;
    padding-left: 5px;
    word-wrap: break-word;
}

.sign {
    -ms-flex: 0 0 82px;
        flex: 0 0 82px;
}

.sign:nth-child(5){
    background-color: #efefff; /*#f3f4fb*/
}

.sign__todo {
    background: url('../../images/icon-17_gray.svg') no-repeat 50% 50%;
    background-size: 25px;
}

.sign__done{
     background: url('../../images/icon-17.svg') no-repeat 50% 50%;
     background-size: 25px;
}

.sign__not_done{
    background: url('../../images/icon-19.svg') no-repeat 50% 50%;
    background-size: 20px;
}

.sign__none{
    background: url('../../images/icon-18.svg') no-repeat 50% 50%;
    background-size: 25px;
}

.basket {
    -ms-flex: 0 0 82px;
        flex: 0 0 82px;
    background: url('../../images/icon-13.svg') no-repeat 50% 50%;
    background-size: 15px;
}

.basket__orange {
    background: url('../../images/icon-13_orange.svg') no-repeat 50% 50%;
    background-size: 15px;
}



