.DaysList {
    background-color: #fff;
    border: 1px solid #cbcbcb;
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    padding: 2px;
    width: 200px;
    position: absolute;
    -webkit-transform: translate3d(260px, 380px, 0px);
            transform: translate3d(260px, 380px, 0px);
    top: 0px;
    left: 0px;
    will-change: transform;
    z-index: 1;
    }

.day {
    border: 1px solid #cbcbcb;
    border-radius: 2px;
    background-color: transparent;
    color: #cbcbcb;
    padding: 2px;
    margin: 4px;
    font-size: 0.75em;
    width: 24px;
    height: 24px;
    outline: none;
}

.dayBtnActive {
    background-color: #303f9f;
    color: #fff;
    outline: none;
}



